/* eslint-disable no-unused-vars */
import React, { useState, useEffect } from 'react';
import {Navbar, Container, Nav, NavDropdown, Button} from 'react-bootstrap';
import './App.css';
import info from './data';
import Items from './items';
import { Link, Route ,Switch, Routes} from 'react-router-dom';
import Detail from './Detail';
import axios from 'axios';


function App() {

  let [행복, 행복변경] = useState(info);
  let [url, url변경] = useState([
      'https://images.unsplash.com/photo-1517677129300-07b130802f46?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1170&q=80',
      'https://images.unsplash.com/photo-1591035897819-f4bdf739f446?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1170&q=80',
      'https://images.unsplash.com/photo-1604699229817-27301bdfed68?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1171&q=80',
      'https://images.unsplash.com/photo-1633114127188-99b4dd741180?ixid=MnwxMjA3fDF8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1170&q=80',
      'https://images.unsplash.com/photo-1636225064817-472339f88d31?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1170&q=80',
      'https://images.unsplash.com/photo-1636219563654-1de28b9b6abe?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1170&q=80'
  ]);
  let [재고, 재고변경] = useState([10,11,12]);


  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Container>
          <Navbar.Brand href="#home">행복상점</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link><Link to="/">Home</Link></Nav.Link>
              <Nav.Link><Link to="/detail">detail</Link></Nav.Link>
              <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Switch>
        
        <Route path="/detail/:id">
          <Detail shoes={행복} 재고={재고} 재고변경={재고변경} url={url}/>
        </Route>
        
        <Route path="/:id">
          <div>아무거나 적었을때 이거 보여주셈</div>
        </Route>

        <Route path="/"> 
          <div className='jumbo'>
            <h2>20% Season Off</h2>
            <p>Note that the development build is not optimized.
              To create a production build, use npm run build.</p>
            <Button variant="primary">Learn more</Button>{' '}
          </div>
          <div className="container">
            <div className="row">
            {
              행복.map((elem, i)=>{
                return <Items 행복={elem} i={i} src={url[i]} />
              })
            }
              
            </div>
            {
              행복.length < 4
              ? (
                <button className="btn btn-primary" onClick={()=>{
                  axios.get('https://codingapple1.github.io/shop/data2.json')
                  .then((result)=>{
                    행복변경([...행복, ...result.data])
                  })
                  .catch(()=>{
                    console.log('실패했어요');
                  })
                }}>더보기</button>
              )
              : null
            }
            
          </div>
        </Route>
    </Switch>

     

    </div>
  );
}




export default App;
